@keyframes appear {
  0% {
    opacity: 0;
    transform: translate(0, 200px);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

#icon-group {
  width: 100%;
  height: 100%;
  position: relative;
}

.icons {
  height: 100px;
  transition: all 1s;
  position: absolute;
  top: 40%;
  left: 40%;
}

.active #icon-first {
  height: 400px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.active #icon-second {
  filter: blur(2px);
  opacity: .5;
  height: 150px;
  top: 50%;
  left: 50%;
  transform: translate(calc(-50% - 130px), calc(70px - 50%));
}

.active #icon-third {
  filter: blur(5px);
  opacity: .4;
  height: 200px;
  top: 50%;
  left: 50%;
  transform: translate(calc(160px - 50%), calc(20px - 50%));
}

#back-text {
  text-align: center;
  letter-spacing: 25px;
  opacity: .5;
  color: #111;
  filter: blur(3px);
  width: 100%;
  font-size: 150px;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.section:hover .marker {
  opacity: 1;
}

#intro {
  background-color: #232323;
  justify-content: center;
  height: 300px;
}

#display-box {
  flex-flow: row;
  width: 100%;
  margin-top: 40px;
  margin-bottom: 40px;
  display: flex;
  overflow-y: hidden;
}

.display-box-item {
  background-color: #232323;
  border-radius: 10px;
  flex-direction: column;
  flex-shrink: 0;
  align-items: center;
  width: 130px;
  height: 180px;
  margin-left: 80px;
  display: flex;
}

.display-box-item .avatar {
  border-radius: 100%;
  width: 70px;
  height: 70px;
  margin-top: 25px;
}

.display-box-item .nickname {
  text-align: center;
  color: #5c5c5c;
  margin-top: 30px;
}

#future {
  background-color: #232323;
  align-items: center;
  height: 300px;
}

#statistic {
  background-color: #232323;
  height: 300px;
}

.stat-wrapper {
  width: 70%;
}

.stat-wrapper .stat-header {
  margin-top: 20px;
  font-size: 35px;
  font-weight: 200;
}

.stat-wrapper .stat-main {
  font-size: 30px;
  font-weight: 500;
}

#count {
  font-size: 100px;
  font-weight: 600;
}

.stat-wrapper .stat-footer {
  text-align: right;
  margin-top: 40px;
  margin-right: 20px;
  font-size: 18px;
  font-weight: 200;
}

.subtitle .text {
  text-align: center;
  text-shadow: 15px -15px 3px #515151d6;
  width: 100%;
  font-size: 70px;
  font-weight: 600;
}

#best_pos {
  margin-right: 20px;
  font-size: 60px;
  font-weight: 800;
}

#statistic {
  height: 350px;
}

.stat-title {
  letter-spacing: 3px;
  margin: 80px 30px 30px;
  font-size: 60px;
  font-weight: 600;
}

.stat-subtitle {
  letter-spacing: 3px;
  margin: 80px 30px 30px;
  font-size: 40px;
  font-weight: 200;
}

#chart {
  padding: 0;
}

#heatmap1, #heatmap2 {
  border-radius: 10px;
  width: calc(100% - 40px);
  height: 600px;
  margin: 20px;
}

#worst {
  background-color: #2c2c2c;
}

#worst_pos {
  margin-right: 20px;
  font-size: 60px;
  font-weight: 800;
}

.canvas {
