  width: 60%;
  height: 450px;
  margin: 30px;
  display: flex;
}

#footer {
  height: 150px;
}

@media screen and (orientation: portrait) {
  .active #icon-first {
    height: 300px;
  }

  .active #icon-second {
    height: 100px;
    transform: translate(calc(-50% - 100px), calc(30px - 50%));
  }

  .active #icon-third {
    height: 150px;
    transform: translate(calc(80px - 50%), calc(10px - 50%));
  }

  #back-text {
    letter-spacing: 2px;
    filter: blur(1px);
    font-size: 45px;
    position: absolute;
  }

  .stat-title {
    margin: 10px 5px 5px;
    font-size: 30px;
  }

  .stat-subtitle {
    margin: 10px 5px 5px;
    font-size: 20px;
  }

  .canvas {
    justify-content: center;
    width: calc(100% - 10px);
    height: 250px;
    margin: 5px;
    display: flex;
  }

  .stat-wrapper .stat-header {
    margin-top: 10px;
    font-size: 20px;
  }

