  }

  .stat-wrapper .stat-footer {
    margin-top: 15px;
    margin-right: 10px;
    font-size: 12px;
  }

  #statistic {
    height: 250px;
  }

  .subtitle {
    width: 100%;
  }

  .content {
    font-size: 25px;
  }

  #best_pos, #worst_pos {
    margin-right: 5px;
    font-size: 40px;
  }
}

